<template>
     <div id="listingContainer">
          <div
               v-show="!shown"
               id="listing"
          >
               <button @click="createListing">Create a listing</button>
          </div>
          <div id="createListing">
               <div id="head">
                    <input
                         placeholder="Click to add a title"
                         id="input_title"
                         required
                         pattern="^[a-zA-Z0-9_', ]{1,}*$"
                         v-model="titleDescription"
                    />
                    <div id="subtitle">
                         <div id="housing_type">
                              <button
                                   v-for="item in housingTypes"
                                   @click="housingType = item.type"
                                   :class="{highlightSpan:item.type == housingType}"
                              >{{item.name}}</button>
                         </div>

                         <input
                              placeholder="Click to add address"
                              id="input_location"
                              required
                              pattern="^[a-zA-Z0-9_'., ]{1,}*$"
                              v-model="addressString"
                         />
                         <input
                              placeholder="Click to add zipcode"
                              id="input_zipcode"
                              required
                              pattern="(^\d{5}$)|(^\d{9}$)|(^\d{5}-\d{4}$)"
                              v-model="zipcode"
                         />
                         <div id="rating">0.0 / 5.0 (0 Reviews)</div>
                    </div>
               </div>
               <div id="pics_container">
                    <button>Add pic</button>
               </div>
               <div id="descriptions">
                    <div id="sub">
                         <div id="beds">
                              (
                              <span @click="beds++">+</span> /
                              <span @click="removeBeds">-</span>
                              ) {{beds}} beds
                         </div>
                         <div id="baths">
                              (
                              <span @click="baths++">+</span> /
                              <span @click="removeBaths">-</span>
                              ) {{baths}} baths
                         </div>
                         <input
                              placeholder="Click to add square feet"
                              id="input_footage"
                              required
                              type="number"
                              v-model="areaSqft"
                         />
                    </div>
                    <textarea
                         placeholder="Click to add a description"
                         id="input_description"
                         required
                         pattern="^[a-zA-Z0-9_',.!?;:[]%/ ]{1,}*$"
                         v-model="longDescription"
                    />
                    <div>Select all amenities that apply:</div>
                    <button
                         v-for="item in amenityTypes"
                         @click="$set(item, 'selected', !item.selected)""
                         :class="
                         {highlight:item.selected}"
                    >{{item.name}}</button>
                    <div>Select all house rules that apply:</div>
                    <button
                         v-for="item in houseRuleTypes"
                         @click="$set(item, 'selected', !item.selected)""
                         :class="
                         {highlight:item.selected}"
                    >{{item.name}}</button>
                    <div id="submit_container">
                         <span id="listing_error">...</span>
                         <button id="submit" @click="pushListing">submit</button>
                    </div>
                    
               </div>
               <div id="price">
                    <div id="rent">
                         $
                         <input
                              type="number"
                              placeholder="Click to add price"
                              id="input_rent"
                              required
                              v-model="rent"
                         />
                         <span>/ month</span>
                    </div>
                    <div id="length">
                         <input
                              type="number"
                              placeholder="Click to add lease length"
                              id="input_leaseLength"
                              required
                              v-model="minLeaseLength"
                         />
                    </div>
                    <input
                         type="text"
                         placeholder="Click to add available date"
                         id="input_available"
                         required
                         v-model="availableDate"
                    />
               </div>
          </div>
     </div>
</template>

<script>
import axios from "axios";
export default {
     data() {
          return {
               shown: false,
               titleDescription: "",
               addressString: "",
               rent: "",
               minLeaseLength: "",
               availableDate: "",
               longDescription: "",
               beds: 0,
               baths: 0,
               housingType: "",
               housingTypes: [
                    { name: "Bedroom", type: "HOUSING_TYPE_BEDROOM" },
                    { name: "Apartment", type: "HOUSING_TYPE_APARTMENT" },
                    { name: "House", type: "HOUSING_TYPE_HOUSE" }
               ],
               amenities: "",
               amenityTypes: [
                    {
                         name: "Washer",
                         type: "AMENITIES_WASHER",
                         selected: false
                    },
                    { name: "Drier", type: "AMENITIES_DRIER", selected: false },
                    {
                         name: "Pets Allowed",
                         type: "AMENITIES_PETS",
                         selected: false
                    },
                    {
                         name: "Parking",
                         type: "AMENITIES_PARKING",
                         selected: false
                    }
               ],
               houseRules: "",
               houseRuleTypes: [
                    {
                         name: "No Smoking",
                         type: "HOUSE_RULES_SMOKING",
                         selected: false
                    }
               ],
               areaSqft: "",
               zipcode: ""
          };
     },
     methods: {
          removeBeds() {
               if (this.beds > 0) {
                    this.beds--;
               }
          },
          removeBaths() {
               if (this.baths > 0) {
                    this.baths--;
               }
          },
          createListing() {
               anime({
                    targets: "#listing",
                    translateY: "100%",
                    duration: 600,
                    easing: "easeOutCubic",
                    complete: () => {
                         this.shown = true;
                         $("#listingContainer").css("overflow","scroll");
                    }
               });
          },
          pushListing() {
               var formData = {
                    titleDescription: this.titleDescription,
                    addressString: this.addressString,
                    rent: this.rent,
                    minLeaseLength: this.minLeaseLength,
                    availableDate: this.availableDate,
                    longDescription: this.longDescription,
                    beds: this.beds,
                    baths: this.baths,
                    housingType: this.housingType,
                    areaSqft: this.areaSqft,
                    zipcode: this.zipcode
               };
               axios({
                    method: "post",
                    url: this.$store.get("apiUrl") + "listings/register",
                    data: formData,
                    headers: {
                         authorization: this.$store.get("token")
                    }
               })
                    .then(response => {
                         console.log("SUCC");
                    })
                    .catch(function(error) {
                         console.log("pepeHands");
                    });
          }
     },
     mounted() {}
};
</script>

